/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  background-color: #f5f7fa;
  line-height: 1.2;
}
/**
 * For modern browsers
 * 1. The space content is one way to avoid an Opera bug when the
 *    contenteditable attribute is included anywhere else in the document.
 *    Otherwise it causes space to appear at the top and bottom of elements
 *    that are clearfixed.
 * 2. The use of `table` rather than `block` is only necessary if using
 *    `:before` to contain the top-margins of child elements.
 */
.cf:before,
.cf:after {
  content: " "; /* 1 */
  display: table; /* 2 */
}

.cf:after {
  clear: both;
}

/**
 * For IE 6/7 only
 * Include this rule to trigger hasLayout and contain floats.
 */
.cf {
  *zoom: 1;
}
.wrapper {
  width: 100%;
  max-width: 650px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 60px;
  margin-bottom: 200px;
  border: 1;
}
.imag_perfil {
  width: 20rem;
}
.content {
  padding: 0 15px;
  width: 340px;
}
.content h1 {
  margin-top: 0;
}
.tarjeta_presentacion {
  width: 20rem;
  font-family: BLOKKNeue-Regular;
}
.tarjeta_presentacion_info {
  width: 20rem;
  height: 10rem;
  color: #434a54;
}

#left {
  font-size: 22px;
  height: 9rem;
  background-color: #8c52ff;
  width: 3.3rem;
  cursor: pointer;
}
#left h1 {
  transform: translateX(100%);
  transform: translateY(-18%);
  margin-left: 140%;
}
#left img {
  width: 3rem;
  margin-left: 2%;
  transform: translateY(100%);
  color: #fff;
}

.contacto {
  color: #434a54;
}
.contacto_info {
  background-color: #8c52ff;
  width: 20rem;
  padding: 4%;
  font-size: 20px;
  color: #fff;
}

.contacto_detalle img {
  width: 2rem;
}

.content p,
.sidebar p {
  font-family: "BLOKKNeue-Regular";
}
.idiomas {
  width: 20rem;
}
.sidebar {
  padding: 20px;
  width: 300px;
  background-color: #8c52ff;
  color: #fff;
}
.sidebar h3 {
  margin: 0;
}
.content,
.sidebar {
  float: left;
}

/* The sticky */
.sidebar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
